import React from 'react';
import axios from 'axios';
import Movie from './movie';
import "./app.css";

class App extends React.Component {
  state = {
    isLoading: true,
    movies: []  
  };
  
  render() { 
    const {isLoading, movies} = this.state;
    return( 
    <section className="container">
      {isLoading 
      ? (<div className ="loader">
          <span className ="loader_text">Loading...</span>
      </div>
    ):( 
    <div className ="movies">
      {movies.map(movie=>{
         return (<Movie 
          key={movie.id}
          id={movie.id}
          year={movie.year}
          title={movie.title}
          summary={movie.summary}
          poster={movie.medium_cover_image}
          genres={movie.genres}
        />);
      })}
    </div> 
    )}</section>);
  }

  getMovies = async () =>{
    const {  //json 구조가 data / data / movies로 되어있음, es6에서는 이렇게 쓸 수 있음
      data: {
        data: {movies}}
      }= await axios.get("https://yts-proxy.now.sh/list_movies.json?sort_by=rating");
    //this.setState({movies:movies});  //state movies에 axios로 가져온 movies를 넣음
    this.setState({movies, isLoading: false}); //이렇게 써도 동작
  }

  //render가 끝나고 실행 
  componentDidMount(){
    this.getMovies();
    
  }
}


export default App;
